@import "../abstracts/variables";
@import "../abstracts/reset";

.main {
  display: flex;
  height: 100vh;

  .introLeft {
    width: 100%;
    position: relative;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #333;

    .leftWrapper {
      width: 70%;
      //padding: 6rem;
      //padding-left: 5rem;
      height: 45%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .introHello {
        font-size: 2em;
        font-weight: 300;
      }

      .introName {
        font-size: 3rem;
        color: $primary-color;
      }

      .titleCont {
        height: 50px;
        overflow: hidden;

        .titleContWrapper {
          height: 100%;
          animation: move 13s ease-in-out infinite alternate;

          .titleItem {
            display: flex;
            align-items: center;
            height: 50px;
            font-size: 2.5rem;
            font-weight: 700;
            color: $secondary-color;
          }
        }
      }

      .description {
        margin-top: 3em;
        width: 75%;
        padding: 2em;
        border-radius: 5px;
        background-color: $secondary-color;
        //background-color: red;
        color: aliceblue;
        cursor: text;

        p {
          padding-left: 1rem;
          font-weight: 400;
          .span1 {
            color: rgb(230, 173, 89);
            font-weight: 500;
          }

          .span2 {
            color: rgb(58, 176, 255);
            font-weight: 500;
          }
        }
      }

      .iconScroll {
        scale: 1.75;
        width: 30px;
        height: 30px;
        position: absolute;
        bottom: 5rem;
        margin-left: 0.5em;
      }
    }
  }

  .introRight {
    flex: 1;
    position: relative;

    .imgBg {
      clip-path: polygon(
        100% 0%,
        100% 49%,
        100% 100%,
        25% 100%,
        0% 50%,
        25% 0%
      );
      background-color: $primary-color;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
    }

    .rightImg {
      position: absolute;
      margin: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
  }
  100% {
    transform: translateY(-200px);
  }
}

@media screen and (max-width: 480px) {
  .main {
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .introLeft{
      width: 90%;
      margin-bottom: 4em;
      .leftWrapper{
        padding: 10px;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: max-content;
        width: 100%;

        .titleCont{
          height: 50px;
  
          .titleContWrapper{
            text-align: center;
            .titleItem{
              display: flex;
              flex-direction: column;
              justify-content: center;
              height: 50px;
              font-size: 1.5rem;
              font-weight: 400;
            }
          }

        }

        .description{
          display: none;
        }

        .iconScroll{
          bottom: 0em;
        }
      }


    }

    .introRight{
      width: 100%;
    }
  }
}
