@import "../abstracts/variables";
@import "../abstracts/reset";

.t{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid #999;
    background-color: white;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: space-around;

    .tIcon{
        width: 15px;
        height: 15px;
    }

    .tButton{
        width: 25px;
        height: 25px;
        border-radius: 50%;
        background-color: #333;
        position: absolute;
        cursor: pointer;
        left: 0;
    }
    
}

